  background-color: #fc7e7e;
}

.bg-image {
  position: absolute;
  z-index: -1;
  opacity: 0.3;
  width: 100%;
  height: 100%;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
}

.header-image {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-repeat: no-repeat;
  width: 64px;
  height: 64px;
  margin: 16px;
  margin-left: 96px;
  opacity: 0.5;
  flex: 1 0;
}

.title {
  display: flex;
}

.title button {
  flex: 0 0 64px;
}

#add-todo-btn {
  padding: 0;
  border: none;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-repeat: no-repeat;
  opacity: 0.5;
  border-radius: 8px;
  width: 64px;
  height: 64px;
  font-size: 64px;
  margin: 16px;

  display: flex;
  align-items: center;
  justify-content: center;
}

#add-todo-btn:hover {
  background-color: pink;
  opacity: 0.8;
}

.main {
  display: flex;
  flex-direction: column;
}

#content {
  display: flex;
}

.sidebar {
  flex: 20;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 16px;
}

.sidebar h2 {
  color: black;
}

.project-title {
  font-size: 32px;
  margin-bottom: 8px;
  font-weight: bolder;
}

.text {
  color: #e65c5c;
}

#projectlisting {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;
}

.projectlisting-button {
  width: 128px;
  font-size: 32px;
  padding: 8px;
  background: none;
  border: none;
}

.projectlisting-button:hover {
  color: pink;
}

#container {
  flex: 80;
  display: flex;
  flex-wrap: wrap;
  margin: 0px auto;
  padding: 32px;
  gap: 32px;
}

.task {
  height: 100px;
  width: 200px;
  background-color: #e65c5c;
  padding: 16px 32px;
  display: flex;
  flex-direction: column;
  align-items: start;
}

.task:hover {
  box-shadow: 0px 0px 5px 5px pink;
}

.task h3 {
  font-weight: bolder;
  font-size: 24px;
  margin-bottom: 16px;
}

.low {
}

.med {
}

.high {
}

.expanded {
}

.todo-card-delete-button {
  align-self: flex-end;
  border: none;
  background: none;
}

.todo-card-delete-button:hover {
  color: pink;
}

.checkbox-div {
  display: flex;
}

.project-list-item-button {
  height: 80%;
  align-self: center;
}

.project-list-item-view-controls {
  display: flex;
}

.dialog-popup {
  margin: 0; /* \`margin: auto;\` is the default. */
  transform: translateX(-50%) translateY(-50%);
  left: 50%;
  top: 50%;
}

.dialog-content {
  display: flex;
  flex-direction: column;
}

dialog input {
  border: 1px solid black;
  padding: 2px;
}

.close-button {
  align-self: flex-end;
  padding: 0;
  border: none;
  background: none;
}

.close-button:hover {
  color: pink;
}
